<template>
    <div class="navContainer">
        <div class="item" v-for="(item,index) in navlist.arr" :key="item.text" @mouseenter="itemMouseEnter(index)" @mouseleave="itemMouseLeave">
            <img name="home-outline" :src="item.icon" class="icon" />
            <div class="text">{{item.text}}</div>
        </div>
        <div class="indicator" :style="navlist.styleObject"></div>
        <div class="right-menu">
            <div class="menu-top">menu</div>
            <div class="menu-items">
                <div class="menu-item">
                    login
                </div>
                <div class="menu-item">
                    about
                </div>
            </div>
        </div>
    </div>
</template>

<script>
import { useRouter } from 'vue-router'
import { nextTick, defineComponent } from 'vue'
import homePageJson from '../assets/json/homepage.json';
import { ref, reactive } from 'vue'
export default defineComponent({
    name: 'topNavs',
    setup(props, context) {
        const router = useRouter()
        let currentStyle = ref()
        const navlist = reactive({
            arr: [],
            styleObject:{}
        })
        navlist.arr = homePageJson.navBar
        currentStyle.value = '0px'
        function handle() {
            nextTick(() => {
                const list = document.querySelectorAll('.item')
                //console.log(list)
                list.forEach((item, index) => {
                    item.addEventListener('click', function () {
                        list.forEach((item) => {
                            item.classList.remove('active')
                        })
                        router.push(navlist.arr[index].url)
                        //context.emit('navClick', index)
                    })
                })
                function activeLink(ind) {

                }
            })
        }
        function itemMouseEnter(item){
            navlist.styleObject = {
                "transform": "translateX(calc(" + 0.92 * item + "rem))",
                "box-shadow": "0 15px 25px $color-away"
            }
            //console.log(item)
        }
        function itemMouseLeave(){
            let currentPageIndex = 0
            for(var i=0;i<navlist.arr.length;i++){

                if(navlist.arr[i].name == router.currentRoute.value.name){
                    currentPageIndex = i
                   // console.log('命中')
                }
            }
            navlist.styleObject = {
                "transform": "translateX(calc(" + currentPageIndex * 0.92 + "rem))",
                "box-shadow": "0 15px 25px $color-away"
            }
            //console.log(router.currentRoute.value.name)
            //console.log(currentPageIndex)
        
        }

        handle()
        itemMouseLeave()
        //console.log(router.currentRoute.value.name)
        return { handle, currentStyle, navlist ,itemMouseEnter,itemMouseLeave }
    }
})
</script>

